.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.scene-background {
  width: 100%;
  height: 100%;
  background-color: green;
}

.sky {
  background-color: lightblue;
  height: 80vh;
}

@font-face {
  font-family: 'cloud-font';
  src: url('AlphaSmoke.TTF') format('truetype');
}

.cloud-text {
  font-family: 'cloud-font';
  font-size: 3.5em;
  font-weight: 500;
  color: white;
  padding-top: 3vh;
  padding-left: 10px;
  padding-right: 10px;
}

.cloud-text.dark {
  color: black;
  position: absolute;
  bottom: 40vh;
  -webkit-animation: flyin 1.5s ease-out forwards;
          animation: flyin 1.5s ease-out forwards;
}

.raindrop {
  background-color: darkblue;
  width: 2px;
  height: 10px;
  -webkit-animation: rain infinite 1s;
          animation: rain infinite 1s;
  margin-right: 10px;
}

.raindrops {
  display: -ms-flexbox;
  display: flex;
  height: 30px;
  width: 110px;
  -ms-flex-flow: wrap;
      flex-flow: wrap;
}

.raindrop.delay-1 {
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}

.raindrop.delay-2 {
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}

.raindrop.shiftx-1 {
  margin-left: 5px;
}

.raindrop.shifty-1 {
  margin-top: 8px;
}

@-webkit-keyframes rain {
  from { opacity: 1; -webkit-transform: translateY(0px); transform: translateY(0px) }
  to { opacity: 0; -webkit-transform: translateY(10px); transform: translateY(10px) }
}

@keyframes rain {
  from { opacity: 1; -webkit-transform: translateY(0px); transform: translateY(0px) }
  to { opacity: 0; -webkit-transform: translateY(10px); transform: translateY(10px) }
}

.cloud-text.dark.exit {
  -webkit-animation: shrink 1s ease-in forwards;
          animation: shrink 1s ease-in forwards;
  left: calc(40vw - 44px);
  -webkit-transform-origin: 60px 120px;
      -ms-transform-origin: 60px 120px;
          transform-origin: 60px 120px;
}

@-webkit-keyframes flyin {  
  from { left: 110vw; }
  to { left: calc(40vw - 44px); }
}

@keyframes flyin {  
  from { left: 110vw; }
  to { left: calc(40vw - 44px); }
}

@-webkit-keyframes shrink {
  from { -webkit-transform: scale(1); transform: scale(1) }
  to { -webkit-transform: scale(0); transform: scale(0) }
}

@keyframes shrink {
  from { -webkit-transform: scale(1); transform: scale(1) }
  to { -webkit-transform: scale(0); transform: scale(0) }
}


@-webkit-keyframes passby {
  from { opacity: 1; -webkit-transform: translateX(100vw); transform: translateX(100vw) }
  to { opacity: 1; -webkit-transform: translateX(-100vw); transform: translateX(-100vw) }
}


@keyframes passby {
  from { opacity: 1; -webkit-transform: translateX(100vw); transform: translateX(100vw) }
  to { opacity: 1; -webkit-transform: translateX(-100vw); transform: translateX(-100vw) }
}

@-webkit-keyframes flipped-passby {
  from { opacity: 1; -webkit-transform: scaleX(-1) translateX(-100vw); transform: scaleX(-1) translateX(-100vw) }
  to { opacity: 1; -webkit-transform: scaleX(-1) translateX(100vw); transform: scaleX(-1) translateX(100vw) }
}

@keyframes flipped-passby {
  from { opacity: 1; -webkit-transform: scaleX(-1) translateX(-100vw); transform: scaleX(-1) translateX(-100vw) }
  to { opacity: 1; -webkit-transform: scaleX(-1) translateX(100vw); transform: scaleX(-1) translateX(100vw) }
}


.passby {
  -webkit-animation-name: passby;
          animation-name: passby;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

.repeat {
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.speed-8 {
  -webkit-animation-duration: 8s;
          animation-duration: 8s;
}

.delay-2000 {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  opacity: 0;
}

.cat,
.raccoon,
.meerkat,
.bridge,
.statue-of-liberty {
  position: absolute;
  width: 70px;
  bottom: 10vh;
  left: 10vw;
  opacity: 0;
  -webkit-animation: passby 15s forwards;
          animation: passby 15s forwards;
}

.cat {
  bottom: 15vh;
  width: 50px;
}
.raccoon {
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
  width: 70px;
}

.meerkat {
  bottom: 6vh;
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
  -webkit-animation-name: flipped-passby;
          animation-name: flipped-passby;
}

.meerkat.second {
  bottom: 8vh;
  -webkit-animation-delay: 6.5s;
          animation-delay: 6.5s;
}

.statue-of-liberty {
  width: 100px;
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}

.bridge {
  width: 150px;
  bottom: 22vh;
}


